.home-page{position:relative}.home-page h1{position:fixed;top:0;left:1em;font-size:2.5rem;font-weight:100;color:#222;width:0;white-space:nowrap;overflow:hidden;-webkit-animation:showHeader 4s ease forwards 1;animation:showHeader 4s ease forwards 1;-webkit-animation-delay:6s;animation-delay:6s}@-webkit-keyframes showHeader{to{width:100%}}@keyframes showHeader{to{width:100%}}@media screen and (max-width: 600px){.home-page h1{transform:rotate(270deg);transform-origin:left;top:70vh;left:24px}@-webkit-keyframes showHeader{to{width:100vh}}@keyframes showHeader{to{width:100vh}}}@media screen and (max-width: 600px) and (max-height: 600px){.home-page h1{font-size:2rem}}@media screen and (max-width: 600px) and (max-height: 460px){.home-page h1{font-size:1.5rem}}@media screen and (max-width: 600px) and (max-height: 340px){.home-page h1{font-size:1.2rem;top:90vh}}body.home{overflow:hidden}

.svg-scene{height:100vh;max-height:1280px}.svg-scene svg{width:100%;height:100%;overflow:hidden;-webkit-animation:sky 2s ease forwards;animation:sky 2s ease forwards;-webkit-animation-delay:4s;animation-delay:4s;background-image:radial-gradient(at top right, #458f94, #e2fdff, #e2fdff);background-position:bottom left;background-size:300%}@-webkit-keyframes sky{to{background-position:center center;background-size:100%}}@keyframes sky{to{background-position:center center;background-size:100%}}.svg-scene svg .grass{transform:translateY(calc(100% - 120px))}.svg-scene svg .grass .blade{-webkit-animation:grow, wobble infinite alternate;animation:grow, wobble infinite alternate;transform-origin:50% 100%}@-webkit-keyframes grow{from{transform:scaleY(0)}to{transform:scaleY(1)}}@keyframes grow{from{transform:scaleY(0)}to{transform:scaleY(1)}}@-webkit-keyframes wobble{from{transform:translateY(0)}to{transform:translateY(20px)}}@keyframes wobble{from{transform:translateY(0)}to{transform:translateY(20px)}}.svg-scene svg .sun{fill:yellow;transform-origin:150px 150px;-webkit-animation:moveIn, shine;animation:moveIn, shine;-webkit-animation-fill-mode:forwards, forwards;animation-fill-mode:forwards, forwards;-webkit-animation-duration:5s, 4s;animation-duration:5s, 4s;-webkit-animation-delay:0s, 5s;animation-delay:0s, 5s;-webkit-animation-direction:normal, alternate;animation-direction:normal, alternate;-webkit-animation-iteration-count:1, infinite;animation-iteration-count:1, infinite;-webkit-animation-timing-function:ease-in-out, ease;animation-timing-function:ease-in-out, ease}@-webkit-keyframes moveIn{0%{transform:translate(0, 0) scale(0.5)}100%{transform:translate(100vw, -180px) scale(1)}}@keyframes moveIn{0%{transform:translate(0, 0) scale(0.5)}100%{transform:translate(100vw, -180px) scale(1)}}@-webkit-keyframes shine{0%{fill:yellow}100%{fill:gold}}@keyframes shine{0%{fill:yellow}100%{fill:gold}}@media screen and (max-width: 700px){.svg-scene svg .sun{-webkit-animation-duration:4s, 4s;animation-duration:4s, 4s;-webkit-animation-delay:0s, 4s;animation-delay:0s, 4s}@-webkit-keyframes moveIn{0%{transform:translate(0, 0) scale(0.4)}100%{transform:translate(calc(100vw - 40px), -180px) scale(0.7)}}@keyframes moveIn{0%{transform:translate(0, 0) scale(0.4)}100%{transform:translate(calc(100vw - 40px), -180px) scale(0.7)}}}.svg-scene svg .sun .sun-shine{stroke:yellow;stroke-width:2px}.svg-scene svg .sun .sun-shine:nth-of-type(even){stroke:gold}.svg-scene svg .cloud{cursor:pointer;transform:translate(100vw, 0);-webkit-animation-name:moveInFL, cloudColorChange;animation-name:moveInFL, cloudColorChange;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-timing-function:ease;animation-timing-function:ease;-webkit-animation-direction:normal;animation-direction:normal;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.svg-scene svg .cloud:nth-of-type(odd){-webkit-animation-name:moveInFR;animation-name:moveInFR;transform:translate(-100vw, 0)}@-webkit-keyframes moveInFR{from{transform:translate(100vw, 0)}to{transform:translate(0, 0)}}@keyframes moveInFR{from{transform:translate(100vw, 0)}to{transform:translate(0, 0)}}@-webkit-keyframes moveInFL{from{transform:translate(-100vw, 0)}to{transform:translate(0, 0)}}@keyframes moveInFL{from{transform:translate(-100vw, 0)}to{transform:translate(0, 0)}}.svg-scene svg .cloud text{font-size:1.4rem;fill:#222}.svg-scene svg .cloud .base,.svg-scene svg .cloud .big,.svg-scene svg .cloud .small{fill:#fff;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s;-webkit-animation-delay:2s, 4s;animation-delay:2s, 4s;-webkit-animation-iteration-count:infinite, infinite;animation-iteration-count:infinite, infinite;-webkit-animation-timing-function:ease, ease;animation-timing-function:ease, ease;-webkit-animation-direction:alternate, alternate;animation-direction:alternate, alternate;-webkit-animation-fill-mode:forwards, forwards;animation-fill-mode:forwards, forwards}@-webkit-keyframes cloudColorChange{0%{fill:#fee}50%{fill:#fff}100%{fill:#eff}}@keyframes cloudColorChange{0%{fill:#fee}50%{fill:#fff}100%{fill:#eff}}.svg-scene svg .cloud .base{-webkit-animation-name:wobbleBase, cloudColorChange;animation-name:wobbleBase, cloudColorChange;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s}@-webkit-keyframes wobbleBase{from{transform:translate(-5px, 0px)}to{transform:translate(5px, 0px)}}@keyframes wobbleBase{from{transform:translate(-5px, 0px)}to{transform:translate(5px, 0px)}}.svg-scene svg .cloud .big{-webkit-animation-name:wobbleBig, cloudColorChange;animation-name:wobbleBig, cloudColorChange;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s}@-webkit-keyframes wobbleBig{from{transform:translate(-5px, 5px)}to{transform:translate(0px, 0px)}}@keyframes wobbleBig{from{transform:translate(-5px, 5px)}to{transform:translate(0px, 0px)}}.svg-scene svg .cloud .small{-webkit-animation-name:wobbleSmall, cloudColorChange;animation-name:wobbleSmall, cloudColorChange;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s}@-webkit-keyframes wobbleSmall{from{transform:translate(10px, 10px)}to{transform:translate(0px, 0px)}}@keyframes wobbleSmall{from{transform:translate(10px, 10px)}to{transform:translate(0px, 0px)}}.svg-scene .ground{height:100%;background:green;margin-top:-4px}

.loading{transition:opacity 2s ease;position:absolute;display:flex;justify-content:center;align-items:center;width:100%;height:100%;z-index:99998}.loading:not(.loading-fs)[data-active=yes]{display:flex}.loading:not(.loading-fs)[data-active=no]{display:none}.loading-fs{background-color:var(--body-bg-color);width:100vw;height:100vh;position:fixed;top:0;left:0;bottom:0;right:0;z-index:99999}.loading-fs[data-active=yes]{opacity:1}.loading-fs[data-active=no]{left:100vw;opacity:0;transition:opacity 2s ease, left 0s linear 2s}

.page nav{padding:1em}

.markdown{color:#333;font-family:'Zilla Slab', serif;font-size:1.1rem;width:100%;margin:0 auto;width:100%;max-width:700px;padding:1rem}.markdown>div>*{text-align:center}@media screen and (max-width: 700px){.markdown>div>*{text-align:left}}.markdown h1,.markdown h2,.markdown h3,.markdown h4,.markdown h5,.markdown h6{font-family:'Baskervville', serif;font-weight:400;color:#222;text-align:center;margin-top:1rem;margin-bottom:.5rem;font-style:italic}@media screen and (max-width: 700px){.markdown h1,.markdown h2,.markdown h3,.markdown h4,.markdown h5,.markdown h6{text-align:left}}.markdown h1.hidden,.markdown h2.hidden,.markdown h3.hidden,.markdown h4.hidden,.markdown h5.hidden,.markdown h6.hidden{opacity:0;color:transparent;height:0}.markdown h1{font-size:2.8rem}.markdown h2{font-size:2.5rem}.markdown h3{font-size:2rem}.markdown h4{font-size:1.5rem}.markdown h5{font-size:1.3rem}.markdown h6{font-size:1.2rem}.markdown ul,.markdown ol{margin-top:1rem;margin-bottom:1rem;text-align:left}.markdown ul ul,.markdown ul ol,.markdown ol ul,.markdown ol ol{margin-top:0;margin-bottom:0}.markdown ul{margin-left:8px;list-style:none}.markdown ul li{position:relative;padding-left:8px}.markdown ul li::before{position:absolute;left:-8px;content:'*'}.markdown ol{margin-left:16px}.markdown a:-webkit-any-link{color:currentColor;text-decoration:underline}.markdown a,.markdown a:link,.markdown a:visited,.markdown a:active,.markdown a:any-link,.markdown a:focus{color:currentColor;text-decoration:underline}.markdown code{background-color:#ddd;color:#444;padding:0 4px;font-family:monospace}.markdown pre{padding:8px;margin:1rem 0;text-align:left;overflow-x:auto;background-color:#ddd}.markdown pre code{padding:0;background-color:transparent;font-size:1em}.markdown pre code .hljs-comment{color:#777}.markdown blockquote{background-color:rgba(200,200,100,0.3);border-left:4px solid #4e4e1e;padding:.5rem 1rem .5rem 2rem;margin:1rem 0;font-style:italic}@media screen and (max-width: 700px){.markdown blockquote{padding-left:1rem;border-left:none}}.markdown img{max-width:100%}@media screen and (max-width: 700px){.markdown img{max-width:100vw;margin-left:-1rem}}.markdown hr{margin:2rem 0;background-color:#666;border:none;height:1px}.markdown table{border-collapse:collapse;margin:0 auto}.markdown table td,.markdown table th{border:1px solid #222;padding:.25rem .5rem}@media screen and (max-width: 700px){.markdown pre,.markdown blockquote,.markdown hr,.markdown .github-user,.markdown .google-map{max-width:100vw;width:100vw;margin-left:-1rem}}

body{margin:0;padding:0}*,*::before,*::after{box-sizing:border-box}a:-webkit-any-link{color:currentColor}a,a:valid,a:any-link,a:visited,a:active{color:currentColor}

